.banner {
  width: 100%;
  height: 576px;

  background-image: url('../../assets/images/banner.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  .banner-content {
    width: 558px;
    background-color: rgba(255, 255, 255, 0.8);
    top: 20%;
    text-align: center;

    @include desktop {
      text-align: start;
    }
  }
}

.introduce
{
  .material-symbols-outlined {
    margin-right: 4px;
    vertical-align: middle;
    font-variation-settings:
    'FILL' 1,
  }
}

.recommend {
 figure {
  width: 120px;
  height: 120px;
  img {
    max-width: 100%;
    height: 100%;
    object-fit: cover;
  }
 }
}

.price {
  height: 600px;
}

.subscribe {
  width: 100%;
  height: 716px;

  background-image: url('../../assets/images/photo03.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  .subscribe-content {
    width: 300px;
    text-align: center;
    top: 28%;

    @include desktop{
      top: 25%;
    }
  }
}
