// line
.divline {
  display: none;
  width: 1px;
  height: 32px;
  background-color: $standard;
  opacity: 0.4;

  @include desktop {
    display: block;
  }
}

// shade
.shade-tertiary {
  width: 100%;
  height: 100%;
  background: rgba(230, 164, 149, 0.8);
  top: 0;
  left: 0;
  padding: 24px;
}
.shade-img {
  background-image: url('../../assets/images/bgimage04.jpg');
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-attachment: fixed;
  padding: 56px 0;
}

// icon
.icon-fill{
  font-variation-settings:
  'FILL' 1,
}

// triangle
.triangle-top {
  position: relative;

  &:after{
    content: '';
    position: absolute;
    top: -26px;
    left: 24px;
    width: 0px;
    height: 0px;
    border-top: 10px solid transparent;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 18px solid #ffffff;

  }
}
