// default-button ex:探索更多
.btn-default-sm {
  display: inline-block;
  text-decoration: none;
  padding: 4px 20px;
  border: 1px solid $standard;
  border-radius: 4px;
  color: $standard;
  background-color: white;
  font-weight: 500;
  transition: all 0.3s;

  &:hover {
    color: $primary;
    background-color: $standard;
  }
}
.btn-default-lg {
  display: inline-block;
  text-decoration: none;
  padding: 12px 40px;
  border: 1px solid $standard;
  border-radius: 4px;
  color: $standard;
  background-color: white;
  font-weight: 500;
  transition: all 0.3s;

  &:hover {
    color: $primary;
    background-color: $standard;
  }
}

// subscription-button ex: try free
.btn-sub {
  display: block;
  text-decoration: none;
  padding: 4px 20px;
  border: 1px solid $tertiary;
  border-radius: 4px;
  color: #ffffff;
  background-color: $standard;
  transition: all 0.3s;

  &:hover {
    background-color: #ffffff;
    color: $standard;
  }
}

// nav-button ex: link
.btn-nav {
  display: block;
  text-decoration: none;
  padding: 4px 8px;
  border: 1px solid transparent;
  border-radius: 4px;
  color: $standard;
  transition: all 0.3s;

  &:hover {
    color: #ffffff;
    background-color: $standard;
    border: 1px solid $standard;
  }

  &:active {
    background-color: #ffffff;
    border: 1px solid $standard;
    color: $standard;
  }
}
